.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  width: 100%;
  height: 80px;
  padding: 0 24px;
  box-sizing: border-box;
  box-shadow: 0 0 20px rgba(0, 0, 0, .07);
  background: #fff;
}

.logo-wrapper {
  position: relative;
  float: left;
  width: 160px;
  height: 18px;
  margin-top: 31px;
  background: url('../../assets/logo.png') left center / auto 18px no-repeat;
}

.logo-wrapper .site-intro {
  position: absolute;
  right: -10px;
  top: -8px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background: url('../../assets/question.png') center center / 100% auto no-repeat;
}

.nav-list {
  float: left;
  margin-left: 40px;
  padding-left: 20px;
  border-left: 1px solid #f5f5f5;
}

.nav-item {
  display: inline-block;
}

.nav-item a {
  display: inline-block;
  line-height: 24px;
  font-size: 16px;
  font-weight: bold;
  padding: 28px 24px 28px 12px;
  text-decoration: none;
  color: #2c2c2c;
}

.nav-item a:hover {
  color: RGBA(49, 63, 165, 1.00);
}

.profile-wrapper {
  float: right;
  width: 240px;
  box-sizing: border-box;
}

.account-address {
  position: relative;
  display: inline-block;
  width: 130px;
  vertical-align: top;
  padding-left: 20px;
  line-height: 80px;
}

.account-address:after {
  position: absolute;
  content: '';
  left: 0;
  top: 50%;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  border-radius: 4px;
  background: RGBA(43, 190, 127, 1.00);
}

.signout-btn {
  float: right;
  cursor: pointer;
  padding: 4px 10px;
  margin-top: 24px;
  border: 1px solid RGBA(254, 51, 47, 1.00);
  border-radius: 20px;
  color: RGBA(254, 51, 47, 1.00);
}

.connect-btn {
  float: right;
  cursor: pointer;
  padding: 4px 10px;
  margin-top: 24px;
  border: 1px solid RGBA(43, 190, 127, 1.00);
  border-radius: 20px;
  color: RGBA(43, 190, 127, 1.00);
}