.stake-container {
  position: relative;
  width: 1200px;
  margin: 20px -100px 0;
}

.stake-pool .title {
  height: 200px;
  background: url('../../assets/stake.jpg') center center / auto 100% no-repeat;
}

.pool-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 40px -10px 0;
}

.pool-list-item {
  width: 32%;
  box-sizing: border-box;
  padding: 0 20px;
  margin-bottom: 40px;
  background-position: center 20px;
  background-size: 48px auto;
  background-repeat: no-repeat;
}

.token-name {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 10px;
}

.token-balance {
  padding-bottom: 20px;
}

.interest-rate {
  padding-bottom: 20px;
  border-bottom: 1px solid #e5e5e5;
}

.item-detail {
  padding: 120px 24px 24px;
  font-size: 16px;
  border-radius: 20px;
  box-sizing: border-box;
  box-shadow: 0 0 20px rgba(0, 0, 0, .07);
  background-color: #fff;
  background-position: center 30px;
  background-size: 64px auto;
  background-repeat: no-repeat;
}

.item-detail p {
  float: right;
  margin: 0;
}

.item-detail p span {
  font-size: 16px;
  color: #949494;
}

.staked-amount, .earned-amount {
  margin-top: 20px;
}

.deposit-btn {
  margin-top: 20px;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  line-height: 48px;
  cursor: pointer;
  color: #fff;
  border-radius: 40px;
  background: RGBA(43, 190, 127, 1.00);
}

/* deposit modal */

.stake-wrapper {
  position: relative;
  width: 400px;
  height: 500px;
}

.selected-token {
  flex: 1;
  height: 38px;
  line-height: 38px;
  padding-left: 48px;
  background-position: 12px center;
  background-size: 24px auto;
  background-repeat: no-repeat;
}

.stake-tabs {
  display: flex;
  padding: 0 24px;
}

.stake-tab-item {
  flex: 1;
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  padding: 12px 0;
  color: #949494;
  cursor: pointer;
  border-bottom: 4px solid transparent;
}

.stake-tab-item.selected {
  color: #2c2c2c;
  border-color: #2c2c2c;
}

.token-placeholder {
  height: 140px;
  background-position: center 40px;
  background-size: 64px auto;
  background-repeat: no-repeat;
}

.stake-wrapper .input {
  padding: 18px 12px;
  font-size: 18px;
}

.stake-btn {
  position: absolute;
  bottom: 40px;
  right: 24px;
  left: 24px;
  font-size: 24px;
  font-weight: bold;
  line-height: 48px;
  text-align: center;
  border-radius: 40px;
  color: #fff;
  cursor: pointer;
  background: #2c2c2c;
}

